<template>
  <!--
    	作者：luoyiming
    	时间：2019-10-25
    	描述：商城管理-编辑商城
    -->
  <el-dialog title="编辑小程序商城" :visible.sync="dialogVisible" @close='dialogFormVisible' :close-on-click-modal="false"
    :close-on-press-escape="false">
    <el-form size="small" :model="form" ref="form" :rules="rules">
      <!--去除浏览器默认-->
      <div style="height: 0; overflow: hidden;">
        <input type="password">
      </div>
      <el-form-item label="商城名称" :label-width="formLabelWidth" prop="app_name">
        <el-input v-model="form.app_name" autocomplete="off" placeholder="请输入商城名称"></el-input>
      </el-form-item>
      <el-form-item label="商家账户名" :label-width="formLabelWidth" prop="user_name">
        <el-input v-model="form.user_name" autocomplete="off" placeholder="请输入商家账户名"></el-input>
        <p class="gray">注：商家后台用户名</p>
      </el-form-item>
      <el-form-item label="商家账户密码" :label-width="formLabelWidth" prop="password">
        <el-input type="password" v-model="form.password" autocomplete="off" placeholder="请输入密码"></el-input>
        <p class="gray">注：商家后台用户密码</p>
      </el-form-item>
      <el-form-item label="确认密码" :label-width="formLabelWidth" prop="password_confirm">
        <el-input type="password" v-model="form.password_confirm" autocomplete="off" placeholder="请输入确认密码"></el-input>
      </el-form-item>

      <el-form-item label="权限列表：" :label-width="formLabelWidth" v-model="form.access_id">
        <el-tree :data="data" show-checkbox node-key="access_id" :default-expand-all="false" :default-checked-keys="select_menu" :props="defaultProps" @check="handleCheckChange"></el-tree>
      </el-form-item>

    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="dialogFormVisible">取 消</el-button>
      <el-button type="primary" @click="addClick()" :loading="loading">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
import ShopApi from '@/api/shop.js'
import Upload from '@/components/file/Upload'

export default {
  components: {
    Upload
  },
  data() {
    /*验证商城名称*/
    let checkAppName = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('请输入商城名称'));
      } else {
        if (this.$filter.isAllSpace(value)) {
          return callback(new Error('不能全是空格'));
        } else {
          callback();
        }
      }
    };

    /*验证用户名*/
    let checkUsername = (rule, value, callback) => {
      if (!this.$filter.replaceSpace(value)) {
        return callback(new Error('商家账户名'));
      } else {
        if (this.$filter.hasSpace(value)) {
          return callback(new Error('不能包含空格'));
        } else {
          callback();
        }
      }
    };

    /*密码验证*/
    let checkPassword = (rule, value, callback) => {
      if (!value) {
        callback();
      } else {
        if (this.$filter.hasSpace(value)) {
          return callback(new Error('不能包含空格'));
        } else if (value.length < 6) {
          return callback(new Error('长度不能小于6位'));
        } else {
          callback();
        }
      }
    };

    /*确认新密码验证*/
    let checkPasswordConfirm = (rule, value, callback) => {
      if (!this.form.password) {
        callback();
      } else if (value !== this.form.password) {
        callback(new Error('确认密码不一致'));
      } else {
        callback();
      }
    };
    return {
      form: {
        access_id: [],
      },
      data: [],
      /*左边长度*/
      formLabelWidth: '120px',
      /*是否显示*/
      dialogVisible: false,
      /*是否正在加载*/
      loading: true,
      defaultProps: {
        children: 'children',
        label: 'name'
      },
      /*权限选中*/
      select_menu: [],
      /*验证规则*/
      rules: {
        app_name: [{validator: checkAppName, required: true, trigger: 'blur'}],
        user_name: [{validator: checkUsername, required: true, trigger: 'blur'}],
        password: [{validator: checkPassword, trigger: 'change'}],
        password_confirm: [{validator: checkPasswordConfirm, trigger: 'blur'}]
      },
      isupload: false
    };
  },
  props: ['open_edit', 'curModel'],
  created() {
    this.dialogVisible = this.open_edit;
    this.form = this.curModel;
    this.getData();
  },
  methods: {
    /*添加商城*/
    addClick() {
      let self = this;
      let params = this.form;
      self.$refs.form.validate((valid) => {
        if (valid) {
          self.loading = true;
          params.is_system_agent = params.is_system_agent ? 1 : 0;
          ShopApi.editShop(params, true).then(res => {
            self.loading = false;
            self.$message({
              message: '恭喜你，修改成功',
              type: 'success'
            });
            self.dialogFormVisible(true);
          })
            .catch(error => {
              self.loading = false;
            });
        }
      });

    },

    //监听选中
    handleCheckChange(data, checked) {
      this.form.access_id = checked.checkedKeys.concat(checked.halfCheckedKeys);
    },
    /*关闭弹窗*/
    dialogFormVisible(e) {
      if (e) {
        this.$emit('closeDialog', {
          type: 'success',
          openDialog: false
        })
      } else {
        this.$emit('closeDialog', {
          type: 'error',
          openDialog: false
        })
      }
    },
    getData() {
      let self = this;
      let params = {}
      params.app_id = self.form.app_id;
      ShopApi.roleInfo(params, true)
        .then(data => {
          let obj = self.clearData(data.data.menu, data.data.select_menu);
          self.select_menu = data.data.select_menu;
          self.data = data.data.menu;
          self.loading = false;
        })
        .catch(error => {
          self.loading = false;
        });
    },
    /*清除数据*/
    clearData(list, authlist) {
      let total = 0;
      let leng = list.length;
      for (let i = 0; i < leng; i++) {
        let item = list[i];
        if (item.children != null) {
          let flag = this.clearData(item.children, authlist);
          if (!flag) {
            let _index = authlist.indexOf(item.access_id);

            if (_index >= 0) {
              authlist.splice(_index, 1);
            }
          }
        }
        if (authlist.indexOf(item.access_id) != -1) {
          total++;
        }
      }
      if (total < leng) {
        return false;
      } else {
        return true;
      }
    },
    /*获取图片*/
    returnImgsFunc(e) {
      console.log(e)
      if (e != null && e.length > 0) {
        this.form.file_url = e[0].file_path
        this.form.system_agent_qrcode = e[0].file_id
      }
      this.isupload = false
    },
    /**
     * 编辑器：选择图片
     */
    onEditorSelectImage: function() {
      this.isupload = true
    },
  }
}
</script>

<style>
.param-img-item .icon img {
  width: 100px;
  height: 100px;
  margin: 0 auto;
}
.draggable-list {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}
.draggable-list .wrapper > span {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.draggable-list .item {
  position: relative;
  width: 110px;
  height: 110px;
  margin-top: 10px;
  margin-right: 10px;
  border-radius: 8px;
  overflow: hidden;
  border: 1px solid #dddddd;
}

.draggable-list .delete-btn {
  position: absolute;
  top: 0;
  right: 0;
  width: 16px;
  height: 16px;
  background: red;
  line-height: 16px;
  font-size: 16px;
  color: #ffffff;
  display: none;
}

.draggable-list .item:hover .delete-btn {
  display: block;
}

.draggable-list .item img {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  max-height: 100%;
  max-width: 100%;
}

.draggable-list .img-select {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px dashed #dddddd;
  font-size: 30px;
}

.draggable-list .img-select i {
  color: #409eff;
}
</style>



