import request from '@/utils/request'

let AccessApi = {
  /*商城列表*/
  shopList(data, errorback) {
    return request._post('/agent/shop/index', data, errorback);
  },
  /*添加商城*/
  addShop(data, errorback) {
    return request._post('/agent/shop/add', data, errorback);
  },
  /*修改商城*/
  editShop(data, errorback) {
    return request._post('/agent/shop/edit', data, errorback);
  },
  /*启用商城*/
  updateStatus(data, errorback) {
    return request._post('/agent/shop/updateStatus', data, errorback);
  },
  /*进入商城*/
  storeEnter(data, errorback) {
    return request._post('/agent/shop/enter', data, errorback);
  },
  /*删除商城*/
  deleteShop(data, errorback) {
    return request._post('/agent/shop/delete', data, errorback);
  },
  /* 权限列表 */
  roleInfo(data,errorback){
    return request._post('/agent/shop/roleInfo',data,errorback);
  },
  /* 权限列表 */
  roleInfoByAdd(data,errorback){
    return request._post('/agent/shop/roleInfoByAdd',data,errorback);
  },
   /*启用测试*/
   isTestChange(data, errorback) {
    return request._post('/agent/shop/updateIsTestStatus', data, errorback);
  },
}

export default AccessApi;



